import React from 'react';

export class Ami extends React.Component {
  render() {
      return (
        <div className="journalContainer">
        <div className="journal">
        <React.Fragment>     
<div class="blog-header">
      <div class="container">
        <h1 class="blog-title">Personnages</h1>
      </div>
    </div>
    </React.Fragment>
  <React.Fragment>
    <div class="container">
      <div >
        <div class="blog-main">
        <nav class="blog-pagination">
        <a class="btn btn-outline-primary" href="/persos">Les Chosen Ones</a>
          <a class="btn btn-outline-primary" href="/divin">Les Divinités</a>
          <a class="btn btn-outline-primary" href="/ami">Les amis</a>
          <a class="btn btn-outline-primary" href="/mechant">Les méchants</a>
          <a class="btn btn-outline-primary" href="/compagnon">Les compagnons</a>
        </nav>
          <div class="blog-post">
              <h2 class="blog-post-title" id="chosen">Les amis</h2>
              <br />
              <div class="miniContainer">
              <h3 className="subtitle">E. Gwiazdowski</h3>
              <p><u>Fonction :</u> Maire</p>
              <p><u>Lieu(x) de rencontre :</u> Sacramenbourg, Îles Lointaines</p>
              <p>E.Gwiazdowski est une magistrate experte en droit, actuellement maire de Sacramenbourg. Elle rencontre les Chosen Ones lors de leur arrivée dans la Contrée principale et les charge de dissoudre le raid Jhâva entravant la ville. Sa phrase fétiche et incontrable est le célèbre proverbe : "Nul ne peut se prévaloir de ses propres turpitudes".</p>
              <p>En plus d'avoir un nom imprononçable, E.Gwiazdowski possède un important éventail d''alinéas en tous genres, dédiés à profondément déstabiliser les ennemis.</p>
              <br />

              <h3 className="subtitle">Nod Ji'Hess</h3>
              <p><u>Fonction :</u> Jhâva renégat</p>
              <p><u>Lieu(x) de rencontre :</u> Sacramenbourg, Îles Lointaines</p>
              <p>Nod a déserté les rangs de l'ordre Jhâva peu de temps après le début du raid de Sacramenbourg. Il est un formidable artisan maîtrisant l'art de la transposition. Les Chosen Ones feront souvent appel à lui pour transposer les vestiges de puissants adversaires, afin d'en tirer une arme, un artefact ou une armure.</p>
              <p>Nod semble toujours avoir une anecdote intéressante à propos de ses anciens camarades tombés au combat, ce qui amène à penser qu'il est resté relativement longtemps au sein de l'ordre.</p>
              <br />

              <h3 className="subtitle">Michel Bay</h3>
              <p><u>Fonction :</u> Directeur de la Tour de Combat</p>
              <p><u>Lieu(x) de rencontre :</u> Tour de Combat</p>
              <p>Michel Bay est un aventurier ayant fait fortune grâce à ses nombreux livres racontant ses périples remplis d'explosions et d'action. Il décide de construire une gigantesque tour en plein désert afin de trouver la guidoune la plus valeureuse du monde, au fil d'une impitoyable série de combats.</p>
              <p>Il faisait partie d'un quatuor de voyageurs aux aventures bourrées de péripétie, mais chacun a suivi une voie différente.</p>
              <br />

              <h3 className="subtitle">P'tit Billy</h3>
              <p><u>Fonction :</u> Aucune, c'est juste un gamin</p>
              <p><u>Lieu(x) de rencontre :</u> Nouillorc, Nowel City, Sainte-Moustache-le-Bosquet</p>
              <p>P'tit Billy est un gamin âgé de 8 ans, vivant avec ses "parents" à Nouillorc. Il rencontre plusieurs fois les Chosen Ones, toujours heureux de l'aider à résoudre ses problèmes quotidiens, sources d'aventures remplies d'amitié et de bons sentiments. Il considère d'ailleurs les Chosen Ones comme ses meilleurs amis, au grand bonheur de ses "parents".</p>
              <p>La présence d'enfants dans l'aventure est toujours un sujet épineux pour Guillaume.</p>
              <br />

              <h3 className="subtitle">Hubert Reeves</h3>
              <p><u>Fonction :</u> Astrophysicien</p>
              <p><u>Lieu(x) de rencontre :</u> Bosquet périphérique, Jhâvasterie, Mont Raelle, Nowel City, Entrelasque</p>
              <p>Hubert Reeves est un astrophysicien de renommée mondial, aussi vieux que bourré de connaissances. Il joue un rôle important dans l'interaction avec les singularités matantesques, qu'il étudie avec le plus grand soin. Selon lui, il existe une force complètement inconnue commune à toutes les singularités.</p>
              <p>Il se joint aux Chosen Ones lors de la bataille d'Entrelasque, puis propose son savoir et ses talents, moyennant de la sweet sweet money.</p>
              <br />

              <h3 className="subtitle">Capitaine Jacques Moineau</h3>
              <p><u>Fonction :</u> Capitaine de la Perle Noire</p>
              <p><u>Lieu(x) de rencontre :</u> Nouillorc, Port Duavre, Takarajima, Îles lointaines</p>
              <p>Un marin très expérimenté devenu capitaine de son propre navire, la Perle Noire. Il fait la liaison entre Nouillorc et le port Duavre, mais s'occupe également d'emmener les voyageurs les plus inconscients vers les terribles Îles lointaines.</p>
              <p>Il mentionne son frère ayant suivi le chemin de la piraterie, vu pour la dernière fois aux alentours de Takarajima il y'a des années.</p>
              <br />

              <h3 className="subtitle">Sylvestre Talon</h3>
              <p><u>Fonction :</u> Directeur de la Pyramide de Combat</p>
              <p><u>Lieu(x) de rencontre :</u> Pyramide de Combat</p>
              <p>Sylvestre est un boxeur vétéran de guerre ayant fait fortune grâce à ses nombreux livres racontant ses périples remplis de muscles et d'action. Il réaménage à lui seul une pyramide à l'abandon afin d'y installer un labyrinthe magique changeant d'aspect à chaque passage, afin de trouver la guidoune la plus maligne du monde.</p>
              <p>Il faisait partie d'un quatuor de voyageurs aux aventures bourrées de péripétie, mais chacun a suivi une voie différente.</p>
              <br />

              <h3 className="subtitle">Chef Jibé</h3>
              <p><u>Fonction :</u> BG de la Vie</p>
              <p><u>Lieu(x) de rencontre :</u> Port Duavre, Îles lointaines</p>
              <p>Le vétéran des 3 BG de la Vie. Chef Jibé rencontre les Chosen Ones au port Duavre, après que les Jhâvas aient scellé l'accès à la route du grand chemin. Il dispose d'une grande connaissance des différents mondes, bien qu'il ne soit qu'n simple observateur.</p>
              <p>En dépit de son statut, il est en mesure d'aider les Chosen Ones à progresser à travers les redoutables Îles lointaines.</p>
              <br />

              <h3 className="subtitle">Jean-Paul</h3>
              <p><u>Fonction :</u> Bonne question</p>
              <p><u>Lieu(x) de rencontre :</u> CCI, Îles lointaines</p>
              <p>Jean-Paul est un individu étrange en étroite relation avec les Divinités Gilles et Guillaume, doté d'un rôle très obscur. Il est en mesure de forger des armes fabuleuses, destinée à chaque Chosen One moyennant des matériaux extrêmement rares.</p>
              <p>Jean-Paul semble être mentionné dans les livres parlant de la Création, signifiant qu'il serait au moins aussi vieux que les Divinités elles-mêmes.</p>
              <br />
              </div>
        </div>
        <nav class="blog-pagination">
            <a class="btn btn-outline-primary" href="#top">Haut de page</a>
          </nav>
        </div>
      </div>
    </div>
  </React.Fragment>
      </div>
      </div>
      );
  }
}

export default Ami;